h2 {
  font-size: 26px;
}

h3 {
  font-size: 22px;
}

.invisible {
  visibility: hidden;
}

.cat-info {
  flex-direction: row;
  justify-content: center;
  gap: 50px;
  display: flex;
}

.cat-info p {
  width: 600px;
  font-size: 20px;
}

.loader {
  width: 100px;
  height: 100px;
  display: inline-block;
  position: relative;
}

.loader:after, .loader:before {
  content: "";
  box-sizing: border-box;
  width: 100px;
  height: 100px;
  background: gold;
  border-radius: 50%;
  animation: animloader 2s linear infinite;
  position: absolute;
  top: 0;
  left: 0;
}

.loader:after {
  animation-delay: 1s;
}

@keyframes animloader {
  0% {
    opacity: 1;
    transform: scale(0);
  }

  100% {
    opacity: 0;
    transform: scale(1);
  }
}

.centered {
  position: fixed;
  top: 50%;
  left: 50%;
}

.breed-select {
  width: 200px;
  height: 30px;
  padding: 5px;
  font-size: 16px;
  position: relative;
}

.breed-select:before {
  content: "Список порід";
  color: #848282;
  font-size: 14px;
  position: absolute;
  top: -20px;
  left: 0;
}

.breed-select:after {
  content: "";
  width: 0;
  height: 0;
  border-top: 5px solid #000;
  border-left: 5px solid rgba(0, 0, 0, 0);
  border-right: 5px solid rgba(0, 0, 0, 0);
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
}

